import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService, SortEvent } from 'primeng/api';

@Component({
  selector: 'dhr-data-table',
  templateUrl: './dhr-data-table.component.html',
  styleUrls: ['./dhr-data-table.component.css']
})
export class DhrDataTableComponent implements OnInit {

  @Input() cols: string[];
  @Input() tableData: any[];
  @Input() paginator: boolean;
  @Input() searchPlaceHolder: string;
  @Input() row: number;
  @Input() showCurrentPageReport: boolean;
  @Input() rowsPerPageOptions: any[];
  @Input() scrollable: boolean;
  @Input() scrollHeight: string;
  @Input() styleClass: string;
  @Input() sortField: string;
  @Input() sortOrder: number;
  @Input() globalFilterFields: any[];
  @Input() headertext: string;
  @Input() subHeadertext: string;
  @Input() contentText: string;
  @Input() actionbtn: boolean;

  @Input() showDeleteAtnBtn: boolean;
  @Input() showDuplicateAtnBtn: boolean;
  @Input() showViewAtnBtn: boolean;
  @Input() showDownloadAtnBtn: boolean;
  @Input() showShareAtnBtn: boolean;

  @Output() deleteEvent = new EventEmitter();
  @Output() duplicateEvent = new EventEmitter();
  @Output() viewEvent = new EventEmitter();
  @Output() downloadEvent = new EventEmitter();
  @Output() shareEvent = new EventEmitter();
  rs: any;

  constructor(public confirmDialog: ConfirmationService) { }

  ngOnInit(): void {
  }
  deleteRow(data) {
    this.rs = null;
    if (document.getElementById('myDropdown')) {
      document.getElementById('myDropdown').remove();
    }
    this.confirmDialog.confirm({
      message: `This will delete ${this.contentText} '<span class="confrm-name">${data.name}</span>' from your records. You can't undo this.`,
      header: `Delete the ${this.contentText}`,
      acceptLabel: 'Delete',
      rejectLabel: 'Cancel',
      acceptIcon: null,
      rejectIcon: null,
      accept: () => {
        this.deleteEvent.emit(data);
      },
      reject: () => {
      }
    });
  }

  duplicateRow(data) {
    this.rs = null;
    if (document.getElementById('myDropdown')) {
      document.getElementById('myDropdown').remove();
    }
    this.confirmDialog.confirm({
      message: `This will create a copy of condenser '<span class="confrm-name">${data.name}</span>'. You will be able to edit it later.`,
      header: 'Duplicate the Condenser',
      acceptLabel: 'Duplicate',
      rejectLabel: 'Cancel',
      acceptIcon: null,
      rejectIcon: null,
      accept: () => {
        this.duplicateEvent.emit(data);
      },
      reject: () => {
      }
    });
  }

  viewRow(data) {
    this.viewEvent.emit(data);
    this.rs = null;
    if (document.getElementById('myDropdown')) {
      document.getElementById('myDropdown').remove();
    }
  }

  downloadRow(data) {
    this.downloadEvent.emit(data);
    this.rs = null;
    if (document.getElementById('myDropdown')) {
      document.getElementById('myDropdown').remove();
    }
  }

  shareRow(data) {
    this.shareEvent.emit(data);
    this.rs = null;
    if (document.getElementById('myDropdown')) {
      document.getElementById('myDropdown').remove();
    }
  }

  showWrappedMenu(r, s) {
    if (document.querySelector('.dropdown-content') == undefined || null) {
      this.rs = s;
    }
    else {
      this.rs = null;
      document.getElementById('myDropdown').remove();
    }
  }
  printFilteredUsers(e) {
    const data = { load: e };
    const event = new CustomEvent('dhr_table_filter_data', { detail: data });
    window.dispatchEvent(event);
  }

  // custom sorting for different field
  customSort(event: SortEvent) {
    console.log(event)
    event.data.sort((data1, data2) => {
      let value1 = data1[event.field];
      let value2 = data2[event.field];
      let result = null;
      console.log(value1, value2)
      if (value1 == null && value2 != null)
        result = -1;
      else if (value1 != null && value2 == null)
        result = 1;
      else if (value1 == null && value2 == null)
        result = 0;
      else if (event.field.includes('updatedAt')) {
        let v1 = new Date(value1);
        let v2 = new Date(value2);
        result = (v1 < v2) ? -1 : (v1 > v2) ? 1 : 0;
      }
      else if (typeof value1 === 'string' && typeof value2 === 'string')
        result = value1.localeCompare(value2);
      else
        result = (value1 < value2) ? -1 : (value1 > value2) ? 1 : 0;

      return (event.order * result);
    });
  }

}